.conv_bar{
    background-color: #22222280;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    height: calc(100vh - 128px);
    align-items: center;
    font-family: 'URW Geometric';
}

.conv_bar .scrol{
    width: 95%;
    height: calc(100vh - 300px);
    overflow: scroll;
    overflow-x: hidden;
}

.top{
    margin: 20px 20px 0px;
    position: relative;
    display: flex;
    align-items: center;
}

.top h2{
    padding-left: 10px;
    color: #B0B0B0;
    font-weight: 700;
    size: 32px;
}

.add{
    position: absolute;
    right: 25px;
    float: right;
    height: 17px;
    width: 17px;
}

.center_top{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 0;
    margin: 0;
}

.center{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
}

.center .search-container {
    display: flex;
    justify-content: start;
    align-items: center;
    height: 50px;
    padding-left: 20px;
    border: 1px solid #FFFFFF50;
    background-color: transparent;
    border-radius: 50px;
    width: 80%;
    margin-bottom: 10px;
    margin-left: -15px;
}

form {
    display: flex;
    align-items: center;
}

.center .search-input {
    border: none;
    background-color: transparent;
    padding: 10px;
    font-size: 16px;
    outline: none;
    color: #B0B0B0;
}

::-webkit-scrollbar {
    width: 8px;
    display: none;
    width: 100%;
}

::-webkit-scrollbar-track {
    background: transparent;
    border-radius: 7px;
}

::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 7px;
}

::-webkit-scrollbar-thumb:hover {
    background: #FFFFFF60;
}

.empty{
    color: #FFFFFF50;
    display: flex;
    text-align: center;
    height: calc(100vh - 275px);
    align-items: center;
    justify-content: center;
}